.main 
{
    display: flex;
    flex-direction: column;
    gap: 25px 0;
    padding: 20px;
    justify-items: center;
}

@media (min-width: 700px)
{
    .main
    {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto auto;
        grid-template-areas: 
        "img0 img1"
        "img2 img3";  
    }

    .main:nth-child(1) { grid-area: img0; } /* Imagem principal */
    .main:nth-child(2) { grid-area: img1; } /* Imagem 1 */
    .main:nth-child(3) { grid-area: img2; } /* Imagem 2 */
    .main:nth-child(4) { grid-area: img3; } /* Imagem 3 */
}
